.title {
  position: relative;
  height: 48px;
  overflow: hidden; }
  .title > #title {
    position: absolute;
    bottom: 4px;
    white-space: nowrap;
    min-width: 100%;
    height: 25px;
    margin: 0;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-animation: title 30s linear -15s infinite;
            animation: title 30s linear -15s infinite; }

@-webkit-keyframes title {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%); }
  35% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%); }
  65% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%); }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%); } }

@keyframes title {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%); }
  35% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%); }
  65% {
    -webkit-transform: translateX(0%);
