@import url("https://fonts.googleapis.com/css?family=Titillium+Web");
html {
  position: relative;
  min-height: 100%; }

body {
  margin-bottom: 75px;
  transition: background-color 1s; }

#AppContainer {
  font-family: 'Titillium Web', sans-serif;
  text-align: center;
  transition: background-color 1s; }

.AppDark {
  background-color: #222;
  color: white; }

.AppLight {
  background-color: white;
  color: #222; }

.player {
  width: 100%;
  height: 72px;
  padding-bottom: 16px; }

.title {
  position: relative;
  height: 48px;
  overflow: hidden; }
  .title > #title {
    position: absolute;
    bottom: 4px;
    white-space: nowrap;
    min-width: 100%;
    height: 25px;
    margin: 0;
    transform: translateX(100%);
    animation: title 30s linear -15s infinite; }

@keyframes title {
  0% {
    transform: translateX(100%); }
  35% {
    transform: translateX(0%); }
  65% {
    transform: translateX(0%); }
  100% {
    transform: translateX(-100%); } }

.error {
  color: #ff0000;
  border-radius: 10px;
  transition: background-color 1s;
  border-color: #ff0000; }
  .AppDark .error {
    background-color: #230000; }
  .AppLight .error {
    background-color: transparent; }

.loading {
  right: 5px; }

#audioQuery {
  margin-top: 100px; }

#greyText {
  margin-top: 100px;
  font-size: 20px;
  transition: color 1s; }
  .AppLight #greyText {
    color: #575757; }
  .AppDark #greyText {
    color: dimgrey; }

#stateText {
  margin-top: 35px;
  margin-bottom: 5px;
  transition: color 1s; }

#changeSkinButton, .lastfm {
  position: fixed;
  top: 17.5px;
  right: 17.5px;
  padding: 5px 6px 0; }
  @media (max-width: 575px) {
    #changeSkinButton, .lastfm {
      position: absolute;
      top: 90px; }
      .AppLight #changeSkinButton, .AppLight .lastfm {
        color: #343a40;
        border-color: #343a40; } }

.lastfm {
  right: 64.5px;
  width: 38px;
  height: 36px;
  padding-top: 0;
  display: flex;
  align-items: center; }
  .lastfm img {
    filter: invert(0);
    transition: filter .2s ease; }
  .lastfm:hover img {
    filter: invert(1); }

#NowPlaying {
  margin-bottom: 10px; }

#playQueue, #searchPanelButton {
  position: absolute;
  bottom: 90px;
  right: 15px;
  z-index: 11;
  transition: border 0.15s, border-color 1s; }
  #playQueue > #arrow, #searchPanelButton > #arrow {
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px; }
  #playQueue.left > #arrow, #searchPanelButton.left > #arrow {
    transform: rotate(135deg); }
  #playQueue.right > #arrow, #searchPanelButton.right > #arrow {
    transform: rotate(-45deg); }
  #playQueue.right > .material-icons, #searchPanelButton.right > .material-icons {
    transform: rotateY(180deg); }
  .AppDark #playQueue.right, .AppDark #searchPanelButton.right {
    background-color: #FFF; }
    .AppDark #playQueue.right .material-icons, .AppDark #searchPanelButton.right .material-icons {
      color: #111; }
    .AppDark #playQueue.right #arrow, .AppDark #searchPanelButton.right #arrow {
      border-color: #111; }
  .AppLight #playQueue.right, .AppLight #searchPanelButton.right {
    background-color: #111; }
    .AppLight #playQueue.right .material-icons, .AppLight #searchPanelButton.right .material-icons {
      color: #FFF; }
    .AppLight #playQueue.right #arrow, .AppLight #searchPanelButton.right #arrow {
      border-color: #FFF; }
  @media (max-width: 575px) {
    #playQueue, #searchPanelButton {
      bottom: 15px; } }
  .AppLight #playQueue > #arrow, .AppLight #searchPanelButton > #arrow {
    border: solid #111;
    border-width: 0 3px 3px 0; }
  .AppLight #playQueue:hover > #arrow, .AppLight #searchPanelButton:hover > #arrow {
    border-color: white; }
    @media (max-width: 575px) {
      .AppLight #playQueue:hover > #arrow, .AppLight #searchPanelButton:hover > #arrow {
        border-color: #111; } }
  .AppDark #playQueue > #arrow, .AppDark #searchPanelButton > #arrow {
    border: solid white;
    border-width: 0 3px 3px 0; }
  .AppDark #playQueue:hover > #arrow, .AppDark #searchPanelButton:hover > #arrow {
    border-color: #111; }
    @media (max-width: 575px) {
      .AppDark #playQueue:hover > #arrow, .AppDark #searchPanelButton:hover > #arrow {
        border-color: white; } }

#searchPanelButton {
  bottom: 134px;
  padding: 5px 5px 0; }
  @media (max-width: 575px) {
    #searchPanelButton {
      bottom: 59px; } }

.btn:focus {
  box-shadow: none; }

.AppDark hr {
  border-top-color: rgba(255, 255, 255, 0.2); }

/* Resonsive Design */
@media only screen and (max-width: 575px) {
  .onoffmode:hover {
    background-color: #222;
    color: inherit; }
  #greyText {
    margin-top: 70px;
    font-size: 20px; }
  #audioQuery {
    margin-top: 50px; }
  .btn-outline-dark {
    background-color: #FFF; }
  .btn-outline-light {
    background-color: #222; }
  .AppLight .btn {
    background-color: #FFF; }
    .AppLight .btn img {
      filter: invert(1); }
    .AppLight .btn:hover, .AppLight .btn:focus {
      color: #222; }
      .AppLight .btn:hover img, .AppLight .btn:focus img {
        filter: invert(1); }
  .AppDark .btn {
    background-color: #222; }
    .AppDark .btn img {
      filter: invert(0); }
    .AppDark .btn:hover, .AppDark .btn:focus {
      color: #fff; }
      .AppDark .btn:hover img, .AppDark .btn:focus img {
        filter: invert(0); } }

.AppHeader {
  height: 75px;
  padding: 20px;
  color: white;
  transition: background-color 1s; }
  .AppDark .AppHeader {
    background-color: #535353; }
  .AppLight .AppHeader {
    background-color: #222; }
  .AppHeader .App-title {
    font-size: 1.5em; }

.AppFooter {
  height: 75px;
  padding: 20px;
  transition: background-color 1s;
  bottom: 0;
  width: 100%;
  position: absolute;
  text-align: center; }
  .AppDark .AppFooter {
    background-color: #181818;
    color: #979797; }
    .AppDark .AppFooter a {
      color: #747474; }
      .AppDark .AppFooter a:hover {
        color: #505050;
        text-decoration: none; }
  .AppLight .AppFooter {
    background-color: #d8d8d8;
    color: #5e5e5e; }
    .AppLight .AppFooter a {
      color: #5d5d5d; }
      .AppLight .AppFooter a:hover {
        color: #343434;
        text-decoration: none; }
  .AppFooter #FooterContent {
    transition: background-color 1s;
    padding: 3px; }
  @media only screen and (max-width: 576px) {
    .AppFooter {
      display: none; } }

#githubLogo {
  width: 26px;
  height: 26px;
  transition: filter 1s; }
  .AppLight #githubLogo {
    filter: invert(1); }

.loader {
  border-left: 4px solid #F6A532;
  border-top: 4px solid #234398;
  border-bottom: 4px solid #E94242;
  border-right: 4px solid #1CACAC;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 0.25s linear infinite;
  margin-top: 35px;
  margin-bottom: 5px; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
