#searchPanel > div {
  height: 100%;
  position: relative; }

#searchPanel .container {
  overflow-x: hidden;
  overflow-y: scroll;
  height: calc(100% - 75px - 2.5em);
  position: relative; }

#searchPanel .search-panel-pagination .btn {
  padding: 0;
  padding-top: 4px; }
  #searchPanel .search-panel-pagination .btn:nth-child(2) {
    padding: 0 12px;
    padding-bottom: 3px; }

#searchPanel .search-panel-pagination:nth-child(1) {
  margin-bottom: 0.25em; }

#searchPanel .result {
  line-height: 1; }
  #searchPanel .result a {
    padding-top: 0.25em; }
  #searchPanel .result .col.row > .col-12 {
    padding: 0; }
