#playQueueList, #searchPanel {
  width: 90%;
  height: calc(100% - 75px);
  position: fixed;
  right: 0;
  top: 0;
  margin-top: 75px;
  z-index: 10;
  transition: background-color 1s, color 1s; }
  @media (min-width: 576px) {
    #playQueueList, #searchPanel {
      width: 288px; } }
  @media (min-width: 768px) {
    #playQueueList, #searchPanel {
      width: 384px; } }
  @media (min-width: 992px) {
    #playQueueList, #searchPanel {
      width: 384px; } }
  @media (min-width: 1200px) {
    #playQueueList, #searchPanel {
      width: 400px; } }
  .AppLight #playQueueList, .AppLight #searchPanel {
    background-color: #d8d8d8; }
  .AppDark #playQueueList, .AppDark #searchPanel {
    background-color: #181818; }
  #playQueueList .edit-icon, #searchPanel .edit-icon {
    position: absolute;
    top: 5px;
    right: 15px; }
    #playQueueList .edit-icon .edit-button, #searchPanel .edit-icon .edit-button {
      background: none;
      color: inherit; }
      #playQueueList .edit-icon .edit-button .material-icons, #searchPanel .edit-icon .edit-button .material-icons {
        font-size: 16px; }
      .AppDark #playQueueList .edit-icon .edit-button.active, .AppDark #searchPanel .edit-icon .edit-button.active {
        background-color: #444; }
      .AppLight #playQueueList .edit-icon .edit-button.active, .AppLight #searchPanel .edit-icon .edit-button.active {
        background-color: #BBB; }
  #playQueueList .list-container, #searchPanel .list-container {
    margin-bottom: 100px;
    padding-left: 15px;
    padding-right: 15px; }
    #playQueueList .list-container .reorder-icon .material-icons, #searchPanel .list-container .reorder-icon .material-icons {
      font-size: 18px; }
    .AppDark #playQueueList .list-container .reorder-icon, .AppDark #searchPanel .list-container .reorder-icon {
      color: grey; }
    .AppLight #playQueueList .list-container .reorder-icon, .AppLight #searchPanel .list-container .reorder-icon {
      color: #333; }
    #playQueueList .list-container .delete-icon, #searchPanel .list-container .delete-icon {
      transition: color .15s ease-in-out; }
      #playQueueList .list-container .delete-icon .material-icons, #searchPanel .list-container .delete-icon .material-icons {
        font-size: 18px; }
      #playQueueList .list-container .delete-icon button, #searchPanel .list-container .delete-icon button {
        padding: 0; }
      .AppDark #playQueueList .list-container .delete-icon, .AppDark #searchPanel .list-container .delete-icon {
        color: #999; }
      .AppLight #playQueueList .list-container .delete-icon, .AppLight #searchPanel .list-container .delete-icon {
        color: #444; }
      .AppDark #playQueueList .list-container .delete-icon button:hover, .AppDark #searchPanel .list-container .delete-icon button:hover {
        color: #E53935; }
      .AppLight #playQueueList .list-container .delete-icon button:hover, .AppLight #searchPanel .list-container .delete-icon button:hover {
        color: #B71C1C; }
      #playQueueList .list-container .delete-icon .btn, #searchPanel .list-container .delete-icon .btn {
        background: none;
        color: inherit; }
